@import url(https://fonts.googleapis.com/css?family=Roboto:500);

@font-face {
  font-family: 'Happiness-Sans-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2205@1.0/Happiness-Sans-Regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Happiness-Sans-Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2205@1.0/Happiness-Sans-Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'Happiness-Sans-Title';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2205@1.0/Happiness-Sans-Title.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

/*
color: #000000, #5d737e, #ffbb00, #f0f7ee, #87bba2
*/

.header-class{position:fixed;width:95vw}
.header-text{float:right;background:rgba(0,0,0,.6);color:#f0f7ee;height:64px;width:670px;border-radius:2px 2px 30px 30px}
.header-class ul{list-style:none;margin:0}
.header-li{font-family:Roboto,sans-serif;margin-right:2rem;padding:1rem;float:right;border:0;cursor:pointer;opacity:.6}
.header-class ul li:hover{opacity:1}
.blank_white{background:transparent;width:40%;height:64px}

body{overflow-x:hidden}
html{scroll-behavior:smooth}
a{cursor: pointer;color:white;text-decoration-line:none}

.bar_menu_container{display:none;justify-content:flex-end}
.bar_open{padding:20px;cursor:pointer;margin:0;}
.bar1,.bar2,.bar3{width:35px;height:5px;background-color:grey;margin:6px 0;transition:.4s}
.bar_close{padding:20px 20px 20px 0;cursor:pointer;margin:0}
.bar_close .bar1{-webkit-transform:rotate(-45deg) translate(-9px,6px);transform:rotate(-45deg) translate(-9px,6px)}
.bar_close .bar2{opacity:0}
.bar_close .bar3{-webkit-transform:rotate(45deg) translate(-8px,-8px);transform:rotate(45deg) translate(-8px,-8px)}
.bar_menu{display:grid;color:grey;font-size:17px;list-style:none;padding:0;grid-gap:21px}
.bar_menu_hide{display:none}
.bar_li{font-family:Roboto,sans-serif;cursor:pointer;color:grey;opacity:.6}

@media (max-width:1040px){
  .header-text{display:none}
  .bar_menu_container{display:grid}
}

@-webkit-keyframes blink{0%,to{border-color:transparent}50%{border-color:#f0f7ee}}
@keyframes blink{0%,to{border-color:transparent}50%{border-color:#f0f7ee}}

.typer{display:flex;justify-content:center;align-items:center;height:100vh;background-image:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url(media/computer-1873831.png);background-attachment:fixed;background-repeat:no-repeat;background-size:cover;color:#f0f7ee;width:100%;flex-direction:column}
.typer-text{font-family:Happiness-Sans-Title,sans-serif;border-right:.1em;font-size:4vw;line-height:1.5;letter-spacing:.1rem;overflow:hidden;padding-top:.2em;}
.home_content{}
.icon{margin:10px;width:78px;height:78px;opacity:.8}

.about_me{display:flex;justify-content:flex-start;margin:0;background:#F2FDFF;height:100%;width:100%}
.about_me_title{font-family:Happiness-Sans-Bold,sans-serif;margin-left:6vw;margin-right:12vw;width:25vw;box-sizing:border-box;text-align:center;align-self:center}
.about_me_hedding{position:relative;display:inline-block;font-size:2.4em;font-weight:400;margin:30px 0}
.about_me_hedding:after{position:absolute;content:"";top:100%;height:2px;width:75px;left:0;right:0;margin:0 auto;background:#101935}
.about_me_content{font-family:Happiness-Sans-Regular,sans-serif;margin-bottom:4rem;margin-top:4rem;box-sizing:border-box;padding:1rem;margin-right:20vw;width:100%}

@media (max-width:576px){
  .about_me{display:inherit;height:100%;width:100%;text-align:center}
  .about_me_title{display:inline-block;text-align:center;margin:0 auto}
  .about_me_content{display:inline-block;margin-top:0;width:100%}
}

.Skill{height:100%;width:100%;margin:0 auto;text-align:center;padding-bottom:5rem;background:#9AD4D6}
.Skill_heeding{font-family:Happiness-Sans-Bold,sans-serif;position:relative;display:inline-block;font-size:2.4em;font-weight:400;margin:30px 0}
.Skill_heeding:after{position:absolute;content:"";top:100%;height:2px;width:50px;left:10%;right:70%;margin:0 auto;background:#564787}
.Skill_image_container{background:#ffffff;text-align:center;display:inline-block;width:114px;height:116px;border-radius:30px 30px;background-size:cover;background-repeat:no-repeat;margin-right:2rem;box-shadow:0 3px 6px rgba(0,0,0,.2),0 3px 6px rgba(0,0,0,.2)}
.Skill_label{display:flex;justify-content:center;align-items:center;background:#000;border-radius:30px 30px;width:100px;height:100px;padding:8px;opacity:.8;font-weight:400;font-size:1rem;color:#fff;visibility:hidden}
.Skill_image_container:hover .Skill_label{visibility:visible;animation:fadeout1 .25s;-moz-animation:fadeout1 .25s;-webkit-animation:fadeout1 .25s;-o-animation:fadeout1 .25s}

@media (max-width:768px){
  .Skill_image_container{width:100px;height:100px;margin:5px}
  .Skill_label{width:84px;height:84px}
}

.Contact{font-family:Happiness-Sans-Regular,sans-serif;height:auto;width:100%;text-align:center;margin:0;padding:0;background:#564787;color:#f0f7ee}
.Contact_hedding{font-family:Happiness-Sans-Bold,sans-serif;position:relative;display:inline-block;justify-content:flex-start;font-size:2.4em;font-weight:400;margin:30px 0}
.Contact_hedding:after{position:absolute;content:"";top:100%;height:2px;width:50px;left:0;right:70%;margin:0 auto;background:#F2FDFF}
.Recommendation_name{padding:0;margin:10px;margin-top:20px}
.Recommendation_detail{font-size:.8em;font-weight:400;padding:0;margin:0}
.Recommendation_content{position:relative;display:inline-block;font-size:1.4em;font-weight:400;margin:0 20% 0 20%}
.Recommendation_section{max-width:70vw;border-radius:4px;margin:0 auto 30px}

.Project{background:#DBCBD8;height:100%;width:100%;}
.Project_title{font-family:Happiness-Sans-Bold,sans-serif;text-align:center;color:#000;margin-bottom:3em}
.Project_hedding{position:relative;display:inline-block;font-size:2.4em;font-weight:400;margin:30px 0}
.Project_hedding:after{position:absolute;content:"";top:100%;height:2px;width:50px;left:50%;right:0;margin:0 auto;background:#9AD4D6}

.Row{flex-wrap:wrap}
.Box,.Row{display:flex}
.Box{height:100%;width:100%;max-width:60vw;border-radius:4px;margin:0 auto 30px;background:#f0f7ee;box-shadow:0 3px 6px rgba(0,0,0,.2),0 3px 6px rgba(0,0,0,.2)}
.Box_image{width:50%;height:auto;}
.image{width:100%;height:100%;}
.Box_info{font-family:Happiness-Sans-Regular,sans-serif;padding:14px;margin-left:2rem}
.Box_content{font-size:1.1em}
.company{font-weight:700;font-size:1.3em}
.name{font-weight:500;font-size:1.1em}
.Box_ul{list-style:none;padding-left:0}
.Box_li{display:inline-block;cursor:pointer;margin-right:4px}
.icon_image{width:30px;height:30px}

@media screen and (max-width:768px){
  .Row{font-size:10px;flex-direction:column}
  .Box,.Row{width:100%}
  .Box{max-width:100%;height:50%;display:grid}
  .Box_image{height:100%;width:100%}
  .image{width:100%;height:240px}
  .Box_title{font-family:Happiness-Sans-Regular,sans-serif;font-weight:500;font-size:14px}
}

@keyframes fadein{0%{opacity:0}to{opacity:1}}
@-webkit-keyframes fadein{0%{opacity:0}to{opacity:1}}
@keyframes fadeout1{0%{opacity:0}to{opacity:.8}}
@-webkit-keyframes fadeout1{0%{opacity:0}to{opacity:.8}}

.HomepageLayout{animation:fadein 1.4s;-moz-animation:fadein 2s;-webkit-animation:fadein 2s;-o-animation:fadein 2s;display:grid;grid-auto-columns:repeat(1,1fr);height:100%;margin:0;padding:0}

